const handleEatMcRib = async (tokenId) => {
  //  ADDED CHECK: Ensure a signer exists for transactions
  if (!selectedNFT || !mcribContract || !contract || !signer) { 
   alert("Wallet is not connected or is read-only. Cannot execute transaction.");
   return;
  }

  setIsEating(true);
  try {
   //  FIX: Use the global 'signer' directly for transactions
   const eatContract = new ethers.Contract(CONTRACT_ADDRESS, ABI, signer);

   const requiredTokens = await eatContract.tokenBalance(tokenId);
   if (requiredTokens.isZero()) throw new Error("This McRib has no tokens remaining!");

   const allowance = await mcribContract.allowance(account, CONTRACT_ADDRESS);

   if (allowance.lt(requiredTokens)) {
    alert("Approving MCRIB tokens for the contract...");
    let txn = await mcribContract.approve(CONTRACT_ADDRESS, requiredTokens.mul(10));
    await txn.wait();
   }

   alert(`Executing eatMcRib for token #${tokenId}...`);
   let txn = await eatContract.eatMcRib(tokenId);
   await txn.wait(); // 
            // 

   alert(`McRib #${tokenId} has been successfully eaten!`);

   // CRITICAL FIX: Refresh all states and the expanded card after transaction confirmation.
   loadBalances();
   await loadOwnedTokenIDs();

   const eatenTokenId = selectedNFT.id;
   const freshNFTMetadata = await fetchAndCacheMetadata(eatenTokenId, contract, false);
   if (freshNFTMetadata) {
    setSelectedNFT(freshNFTMetadata);
   }

   await loadMyMcRibsFast(currentPage); //  NOW USING FAST VERSION

  } catch (error) {
   console.error("Error eating McRib:", error);
   alert(`Failed to eat McRib: ${error.reason || error.message || "Transaction failed."}`);
  } finally {
   setIsEating(false);
  }
 };

