const switchToDogechain = async () => {
    //  CRITICAL FIX: Block all window.ethereum access inside the TMA
    const isTelegramNow =
      typeof window !== "undefined" &&
      window.Telegram &&
      window.Telegram.WebApp;

    if (isTelegramNow) {
        console.log("[TMA] Blocked call to switchToDogechain (MetaMask access).");
        return;
    }
    //  END CRITICAL FIX

    try {
      await window.ethereum.request({
        method: "wallet_switchEthereumChain",
        params: [{ chainId: DOGECHAIN.chainId }]
      });
    } catch (e) {
      if (e.code === 4902) {
        await window.ethereum.request({
          method: "wallet_addEthereumChain",
          params: [DOGECHAIN]
        });
      }
    }
  };

const connectWallet = async () => {
  try {
    //  ULTIMATE PROTECTION CHECK (Checks for both JS object AND URL parameter)
    const isTelegramNow =
      typeof window !== "undefined" &&
      (window.Telegram?.WebApp || window.location.search.includes('tgWebApp'));

    if (isTelegramNow) {
        // If we detect the TMA environment, we exit immediately.
        console.log("[TMA] Blocked manual wallet connection attempt. Auto-connect is running.");
        return; 
    }
    //  END ULTIMATE PROTECTION CHECK

    //  NORMAL BROWSER (MetaMask)  This code only executes if we are NOT in the TMA
    if (!window.ethereum) {
      alert("MetaMask not available"); 
      return;
    }

    await switchToDogechain();

    const web3Provider = new ethers.providers.Web3Provider(window.ethereum);
    await web3Provider.send("eth_requestAccounts", []);

    signer = web3Provider.getSigner();
    provider = web3Provider;
    contract = new ethers.Contract(CONTRACT_ADDRESS, ABI, signer);

    const address = await signer.getAddress();
    setAccount(address);

    await loadBalances();
    await loadMyMcRibsFast(1);

  } catch (e) {
    alert("Wallet connection failed: " + e.message);
  }
};

