// Local cache key
const RECENT_MINTS_KEY = "mcrib_recent_mints";

// Load from cache on startup
const loadRecentMintsFromCache = () => {
  try {
    const raw = localStorage.getItem(RECENT_MINTS_KEY);
    if (!raw) return [];
    return JSON.parse(raw);
  } catch {
    return [];
  }
};

// Save back to cache
const saveRecentMintsToCache = (list) => {
  localStorage.setItem(RECENT_MINTS_KEY, JSON.stringify(list));
};

// =========================================================
// MAIN MINT HANDLER (ONE mint  ONE update, ZERO SPAM)
// =========================================================
const handleMintEvent = async (tokenId) => {
  const idStr = tokenId.toString();
  console.log("[FRESH] Mint event detected:", idStr);

  try {
    // Load the NFT metadata
    const nft = await fetchAndCacheMetadata(idStr, contract, false);
    if (!nft) {
      console.warn("[FRESH] No metadata returned for token", idStr);
      return;
    }

    const now = Date.now();
    const cutoff = now - 24 * 60 * 60 * 1000; // 24h

    // Load previous list
    let list = loadRecentMintsFromCache();
    console.log("[FRESH] BEFORE:", list.length, "recent items");

    // Insert newest first
    list.unshift({
      id: nft.id,
      metadata: nft.metadata,
      timestamp: now
    });

    // Remove older than 24h
    list = list.filter((m) => m.timestamp >= cutoff);

    // Keep only last 20
    if (list.length > 20) {
      list = list.slice(0, 20);
    }

    // Save + update UI
    saveRecentMintsToCache(list);
    setRecentMints(list);

    console.log("[FRESH] AFTER:", list.length, "recent items");
  } catch (err) {
    console.error("[FRESH] Mint event handler FAILED:", err);
  }
};

// =========================================================
// HOOK UP EVENT LISTENER  RUNS ONCE (NO SPAM)
// =========================================================
useEffect(() => {
  if (!contract) return;

  console.log("[FRESH] Installing Transfer listener");

  // Load cached recent mints on startup so UI isnt empty
  const cached = loadRecentMintsFromCache();
  console.log("[FRESH] Startup cache load:", cached.length, "items");
  setRecentMints(cached);

  // ERC721 mint = Transfer(from = 0x0, to = minter, tokenId)
  const filter = contract.filters.Transfer(null, null);

  const onTransfer = (from, to, tokenId) => {
    const idStr = tokenId.toString();

    console.log("[EVENT] Transfer fired:", { from, to, id: idStr });

    // Detect mint
    if (from === ethers.constants.AddressZero) {
      console.log("[EVENT] MINT detected:", idStr);

      // Update mint count (ONE call, no spam)
      loadMintCount();

      // Update Fresh Off the Grill list
      handleMintEvent(tokenId);

      // Refresh wallet NFTs once
      loadMyMcRibsFast(1);
    }
  };

  contract.on(filter, onTransfer);

  return () => {
    console.log("[FRESH] Removing Transfer listener");
    contract.off(filter, onTransfer);
  };
}, [contract]);


  // ====================================================================
  // HELPER FUNCTION: Handles the fetch from the contract and updates the cache.
  // ====================================================================
  const fetchAndCacheMetadata = async (id, contract, isBackgroundUpdate) => {
    try {
      const uri = await contract.tokenURI(id);
      const metadata = parseTokenURI(uri, id.toString());
      const nftData = { id, metadata };
      localStorage.setItem(`mcrib_nft_${id}`, JSON.stringify(nftData));
      return nftData;
    } catch (e) {
      console.error(`Failed to fetch/cache metadata for #${id}:`, e);
      return null;
    }
  };

  // ====================================================================
  // NEW DEDICATED FUNCTION: Loads the raw list of owned token IDs from the contract.
  // ====================================================================
  const loadOwnedTokenIDs = async () => {
    if (!account || !contract) return [];
    try {
      const balance = await contract.balanceOf(account);
      const tokenIds = [];
      for (let i = 0; i < balance.toNumber(); i++) {
        const id = await contract.tokenOfOwnerByIndex(account, i);
        tokenIds.push(id.toNumber());
      }
      setAllTokenIDs(tokenIds);
      return tokenIds;
    } catch (e) {
      console.error("Failed to load owned token IDs:", e);
      return [];
    }
  };

  // ====================================================================
  // INSTANT LOAD  25 NFTs WITH 1 RPC CALL USING MULTICALL
  // ====================================================================
  const loadMyMcRibsFast = async (page = 1) => {
    if (!account || !contract) return;

    setMintStep(`Loading page ${page}`);

    let tokenIds = allTokenIDs;
    if (tokenIds.length === 0) {
      tokenIds = await loadOwnedTokenIDs();
      if (tokenIds.length === 0) {
        setMintStep("No McRibs found");
        return;
      }
    }

    const start = (page - 1) * NFTS_PER_PAGE;
    const end = start + NFTS_PER_PAGE;
    const idsForPage = tokenIds.slice(start, end);
    if (idsForPage.length === 0) {
      setMyNFTs([]);
      setMintStep(null);
      return;
    }

    const multicall = new ethers.Contract(MULTICALL_ADDRESS, MULTICALL_ABI, provider);
    const calls = idsForPage.map(id => ({
      target: CONTRACT_ADDRESS,
      callData: contract.interface.encodeFunctionData("tokenURI", [id])
    }));

    try {
      const [, returnData] = await multicall.aggregate(calls);

      const loaded = [];
      for (let i = 0; i < idsForPage.length; i++) {
        const rawUri = contract.interface.decodeFunctionResult("tokenURI", returnData[i])[0];
        const metadata = parseTokenURI(rawUri, idsForPage[i].toString());
        const fresh = { id: idsForPage[i].toString(), metadata };
        localStorage.setItem(`mcrib_nft_${idsForPage[i]}`, JSON.stringify(fresh));
        loaded.push(fresh);
      }

      setMyNFTs(loaded);
      setCurrentPage(page);
      setMintStep(null);
    } catch (err) {
      console.error("Multicall failed, falling back...", err);
      // Fallback to old method if needed
      const loaded = [];
      for (const id of idsForPage) {
        const fresh = await fetchAndCacheMetadata(id, contract, false);
        if (fresh) loaded.push(fresh);
      }
      setMyNFTs(loaded);
      setCurrentPage(page);
      setMintStep(null);
    }
  };

  // 
  // UNIVERSAL tokenURI PARSER  WORKS EVERY TIME
  // 
  // FORCE PRE-REVEAL SCREEN  bypasses all parsing issues
  const forcePreRevealImage = (tokenId) => {
    return `data:image/svg+xml;base64,`.replace("{{tokenId}}", tokenId);
  };

  // 
  // FINAL UNIVERSAL tokenURI PARSER  WORKS WITH RAW JSON (current contract)
  // ALSO STILL WORKS IF SOMEONE RETURNS BASE64 (future-proof)
  // 
  const parseTokenURI = (uri, tokenId) => {
    try {
      let jsonString;

      if (uri.startsWith("{")) {
        jsonString = uri;
      }
      else if (uri.startsWith("data:application/json")) {
        const b64 = uri.split("base64,")[1];
        jsonString = atob(b64);
      }

      const json = JSON.parse(jsonString);

      return {
        name: json.name || `McRib #${tokenId}`,
        description: json.description || "Fresh McRib. Ready to eat!",
        image: json.image,
        attributes: json.attributes || []
      };
    } catch (e) {
      console.error("Metadata parse failed for token", tokenId, e);
      console.log("RAW URI:", uri);

      return {
        name: `McRib #${tokenId}`,
        description: "Loading",
        image: null,
        attributes: []
      };
    }
  };

const [mintQuantity, setMintQuantity] = useState(1);

const handleMint = async () => {
  if (!account) return alert("Connect wallet first");
  if (mintQuantity < 1 || mintQuantity > 20) {
    return alert("You can only mint 120 McRibs at a time");
  }

  setCooking(true);
  setMintStep(`Preparing grill for ${mintQuantity} McRib${mintQuantity > 1 ? "s" : ""}...`);

  try {
    const mcrib = new ethers.Contract(MCRIB_ADDRESS, ERC20_ABI, signer);

    // ============================
    // DEBUG START  BEFORE MINT
    // ============================
    console.log("===== MULTI-MINT DEBUG START =====");
    console.log(`Minting quantity: ${mintQuantity}`);

    const dogeBefore = await provider.getBalance(account);
    const mcribBefore = await mcrib.balanceOf(account);
    console.log("DOGE BEFORE:", ethers.utils.formatEther(dogeBefore));
    console.log("MCRIB BEFORE:", ethers.utils.formatUnits(mcribBefore, 15));

    const dogeFeePer = await contract.dogeFee();
    const mcribPricePer = await contract.mcribPrice();

    const totalDogeRequired = dogeFeePer.mul(mintQuantity);
    const totalMcribRequired = mcribPricePer.mul(mintQuantity);

    console.log("Per mint  DOGE fee:", ethers.utils.formatEther(dogeFeePer));
    console.log("Per mint  MCRIB price:", ethers.utils.formatUnits(mcribPricePer, 15));
    console.log(`TOTAL REQUIRED  DOGE: ${ethers.utils.formatEther(totalDogeRequired)} | MCRIB: ${ethers.utils.formatUnits(totalMcribRequired, 15)}B`);

    // ============================
    // CHECK REMAINING SUPPLY
    // ============================
    setMintStep("Checking remaining supply...");
    const currentSupply = await contract.totalSupply();
    if (currentSupply.add(mintQuantity).gt(10000)) {
      throw new Error(`Only ${10000 - currentSupply.toNumber()} McRibs left  can't mint ${mintQuantity}`);
    }

    // ============================
    // 1. CHECK NATIVE DOGE BALANCE
    // ============================
    setMintStep("Checking DOGE balance...");
    const nativeBalance = await provider.getBalance(account);
    if (nativeBalance.lt(totalDogeRequired)) {
      throw new Error(
        `Not enough DOGE! Need ${ethers.utils.formatEther(totalDogeRequired)} DOGE (${mintQuantity}  ${ethers.utils.formatEther(dogeFeePer)})`
      );
    }

    // ============================
    // LEGACY GAS & SAFE LIMITS (The Fix: Simple & Separate)
    // ============================
    setMintStep("Setting Dynamic Gas Options...");
    
    // --- Dynamic Gas Limit Calculation ---
    // These constants are estimated based on your contract's logic (looping/random ID generation).
    const PER_MINT_COST = 400000; // Estimated gas cost for one mint loop iteration
    const BASE_OVERHEAD = 500000; // Baseline gas cost for function setup
    const MAX_LIMIT = 10000000;   // Hard cap at 10 Million to prevent sending excessive gas

    const calculatedGasLimit = BASE_OVERHEAD + (mintQuantity * PER_MINT_COST);
    
    // Use the calculated limit, or the MAX_LIMIT if the calculation exceeds it (for safety)
    const finalGasLimit = calculatedGasLimit > MAX_LIMIT ? MAX_LIMIT : calculatedGasLimit;

    // Inherit gasPrice from TX_OPTIONS, then add safe gasLimit for each transaction.
    const approveTxOptions = { ...TX_OPTIONS, gasLimit: ethers.BigNumber.from(400000) }; // 400k safe limit for approval
    const mintTxOptions = { 
      ...TX_OPTIONS, 
      gasLimit: ethers.BigNumber.from(finalGasLimit) // <-- NOW DYNAMICALLY SCALED!
    }; 
    // ============================
    // 2. APPROVE MCRIB (if needed)
    // ============================
    setMintStep("Checking MCRIB approval...");
    const allowance = await mcrib.allowance(account, CONTRACT_ADDRESS);
    
    //  CRITICAL DEBUG LINES 
    console.log("DEBUG: Contract Address Being Checked:", CONTRACT_ADDRESS);
    console.log("DEBUG: Current Allowance:", allowance.toString());
    console.log("DEBUG: Required MCRIB:", totalMcribRequired.toString());
    //          

    if (allowance.lt(totalMcribRequired)) {
      setMintStep(`Approving ${ethers.utils.formatUnits(totalMcribRequired, 15)}B MCRIB...`);
      const approveTx = await mcrib.approve(
        CONTRACT_ADDRESS,
        totalMcribRequired.mul(10), // generous buffer
        approveTxOptions // <-- USES CORRECT, SEPARATE APPROVE OPTIONS
      );
      await approveTx.wait();
      console.log("MCRIB APPROVED FOR", ethers.utils.formatUnits(totalMcribRequired.mul(10), 15) + "B");
    } else {
      console.log("MCRIB already approved");
    }

    // ============================
    // 3. MINT MULTIPLE McRIBS
    // ============================
    setMintStep(`Grilling ${mintQuantity} McRib${mintQuantity > 1 ? "s" : ""}...`);
    console.log("Sending mint tx with value:", ethers.utils.formatEther(totalDogeRequired), "DOGE");

    const mintTx = await contract.mint(mintQuantity, {
      ...mintTxOptions,    //  USES CORRECT, SEPARATE MINT OPTIONS
      value: totalDogeRequired
    });

    setMintStep("On the blockchain... almost there!");
    const receipt = await mintTx.wait();
    // =========================
    // DROP COOKING SCREEN FAST
    // =========================
    setMintStep("McRibs served fresh!");
    setCooking(false); // <-- this is the important part

    console.log("MINT TX SUCCESS:", receipt.transactionHash);

    // ============================
    // DEBUG END  AFTER MINT
    // ============================
    const dogeAfter = await provider.getBalance(account);
    const mcribAfter = await mcrib.balanceOf(account);

    console.log("DOGE AFTER:", ethers.utils.formatEther(dogeAfter));
    console.log("MCRIB AFTER:", ethers.utils.formatUnits(mcribAfter, 15));
    console.log("DOGE DEDUCTED:", ethers.utils.formatEther(dogeBefore.sub(dogeAfter)));
    console.log("MCRIB DEDUCTED:", ethers.utils.formatUnits(mcribBefore.sub(mcribAfter), 15));
    console.log("===== MULTI-MINT DEBUG END =====");

// ============================
// SUCCESS & REFRESH (FINAL NON-LAG FIX)
// ============================

// Drop the cooking screen FAST so UI doesn't freeze
setMintStep("McRibs served fresh!");
setTimeout(() => {
  setCooking(false);
  setMintStep("");
}, 1200);

// Begin background refresh (non-blocking, zero UI lag)
setTimeout(async () => {

  // Still keep your full refresh cycle  just moved to run AFTER overlay closes.
  // CRITICAL FIX: Moving the slow functions to background prevents the freeze.

  // === REAL-TIME REFRESH CYCLE  WITHOUT loadData() ===
  await loadBalances();        // Update DOGE + MCRIB balance
  await loadOwnedTokenIDs();   // Instantly refresh owned IDs
  await loadMyMcRibsFast(1);   // Reload page 1

}, 1500);

// Fire success alert AFTER the new data has started refreshing
setTimeout(() => {
  alert(`${mintQuantity} McRib${mintQuantity > 1 ? "s" : ""} successfully minted!`);
}, 1800);

} catch (error) {
  console.error("Mint failed:", error);

  // Keep your smart fallback error message
  try {
    const fee = await contract.dogeFee();
    const price = await contract.mcribPrice();
    const totalDoge = fee.mul(mintQuantity);
    const totalMcrib = price.mul(mintQuantity);

    alert(
      `Mint failed  Required for ${mintQuantity}:\n` +
      `${ethers.utils.formatEther(totalDoge)} DOGE + ${ethers.utils.formatUnits(totalMcrib, 15)}B MCRIB\n\n` +
      (error.message || "Check wallet & balances")
    );
  } catch {
    alert("Mint failed  check network, balances, or try again.");
  }
} finally {
  // Ensure cooking state auto-clears no matter what
  setTimeout(() => {
    setCooking(false);
    setMintStep("");
  }, 3000);
}
};

